* {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}
body::-webkit-scrollbar{
    display: none;
}

nav {
    position: relative;
    height: 90px;
    width: 100%;
    z-index: 999999999;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: black;
    padding: 0 20px;
    /* padding-left: 200px; */
}

nav>h2 {
    color: rgba(255, 255, 255, 0.698);
    transition: .3s linear;
}

nav>h2:hover {
    scale: 1.1;
}

nav>i {
    font-size: 28px;
    cursor: pointer;
    transition: .5s ease-in-out;
}

nav>i:hover {
    transform: rotate(20deg);

}

nav .search {
    background-color: white;
    height: auto;
    width: auto;
    border-radius: 25px;
    transition: .5s ease;
}

nav .search input {
    height: 45px;
    width: 600px;
    border-bottom-left-radius: 25px;
    border-top-left-radius: 25px;
    /* text-align: center; */
    border: none;
    outline: none;
    padding: 0 20px;
    background-color: rgba(128, 128, 128, 0.262);
}

nav .search button {
    padding: 11px 30px;
    outline: none;
    border: none;
    font-weight: bold;
    background-color: rgba(8, 8, 163, 0.499);
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    color: white;
    transition: .5s ease;
}

nav .search button:hover {
    background-color: rgba(8, 8, 163, 0.724);
}

nav .search:hover {
    box-shadow: 0px 0px 20px gainsboro;
}

nav .social-media {
    /* border: 1px solid white; */
    width: 12%;
    height: 60%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

nav .social-media a {
    color: white;
    font-size: 28px;
    transition: .5s ease-in-out;
}

nav .social-media a:hover {
    transform: rotate(360deg);
}

nav .social-media a:nth-child(1) {
    color: rgba(240, 21, 174, 0.724);
}

nav .social-media a:nth-child(2) {
    color: rgb(85, 0, 255);
}

nav .social-media a:nth-child(3) {
    color: rgb(85, 0, 255);
}

nav .social-media a:nth-child(4) {
    color: rgb(255, 0, 0);
}

.nav2 {
    position: relative;
    width: 100%;
    height: 60px;
    background-color: rgb(0, 0, 0);
}

.nav2 .ul-list {
    width: auto;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav2 .ul-list ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    list-style: none;
    padding: 11px 0px 0px 0px;
}

.nav2 .ul-list ul li>a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.667);
    font-size: 20px;
    transition: .3s linear;
}

.nav2 .ul-list ul li>a:hover {
    color: white;
}

.nav2 .ul-list ul button {
    background-color: transparent;
    border: none;
    outline: none;
    color: rgba(255, 255, 255, 0.667);
    font-size: 20px;
}

.nav2 .ul-list ul  .button{
   color: white
}

.card{
    border: none;
    box-shadow: 0px 0px 50px rgba(250, 44, 7, 0.383);
}

